@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; }

.gallery {
  width: 860px;
  max-width: 100%;
  margin: 0 auto; }

.gallery-header {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  gap: 20px;

  &__input {
    width: 100%; } }

.gallery-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  gap: 5px;
  list-style: none;

  &__item {
    position: relative;
    min-width: 30px;
    min-height: 30px;
    height: 25vh;
    flex-grow: 1;
    cursor: pointer;
    background: url('load.png') no-repeat 50% 50% {
      color: grey; }

    &:hover {
      .gallery-list__image {
        -webkit-filter: brightness(70%); } }

    &:hover .gallery-list__close {
      opacity: 1; }

    &:last-child {
      flex-grow: 10; } }

  &__close {
    font-size: 1.4em;
    background: none;
    border: none;
    position: absolute;
    top: 8px;
    right: 10px;
    cursor: pointer;
    color: white;
    opacity: 0.8;
    z-index: 1; }

  &__image {
    height: 100%;
    min-width: 100%;
    object-fit: cover;
    vertical-align: bottom;
    -webkit-filter: brightness(100%);
    transition: all .7s ease;

    &--poly {
      font-family: 'object-fit: cover'; } } }

.gallery-drop {
  position: relative;
  display: flex;
  border: 2px #000000 dashed;
  width: 100%;
  min-height: 40px;
  text-align: left;
  justify-content: space-between;
  margin-bottom: 20px;
  cursor: pointer;

  .gallery-drop__info {
    color: #8d8888;
    padding: 10px;
    pointer-events: none; }

  .gallery-drop__input {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer; } }

@media (max-width: 880px) {
  .gallery {
    padding: 0 10px; } }

@media (max-height: 680px) {
  .gallery-list {
    &__item {
      height: 40vh; } } }

@media (max-height: 480px) {
  .gallery-list {
    &__item {
      height: 60vh; } } }

@media (max-aspect-ratio: 1/1) and (max-width: 480px) {
  .gallery-list {
    flex-direction: row;

    &__item {
      height: auto;
      width: 100%; }

    &__image {
      width: 100%;
      min-width: 0; } } }

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .gallery-header {
    &__input {
      width: calc(100% - 130px); }

    &__button {
      width: 70px; } }

  .gallery-list {
    &__item {
      height: 25vh;
      margin-right: 5px;
      margin-bottom: 5px; } } }
