@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*{margin:0;padding:0;font-family:'Roboto', sans-serif;box-sizing:border-box}.gallery{width:860px;max-width:100%;margin:0 auto}.gallery-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:20px 0;grid-gap:20px;gap:20px}.gallery-header__input{width:100%}.gallery-list{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:0;grid-gap:5px;gap:5px;list-style:none}.gallery-list__item{position:relative;min-width:30px;min-height:30px;height:25vh;-webkit-flex-grow:1;flex-grow:1;cursor:pointer;background:url(data:image/png;base64,) no-repeat 50% 50%;background-color:grey}.gallery-list__item:hover .gallery-list__image{-webkit-filter:brightness(70%)}.gallery-list__item:hover .gallery-list__close{opacity:1}.gallery-list__item:last-child{-webkit-flex-grow:10;flex-grow:10}.gallery-list__close{font-size:1.4em;background:none;border:none;position:absolute;top:8px;right:10px;cursor:pointer;color:white;opacity:0.8;z-index:1}.gallery-list__image{height:100%;min-width:100%;object-fit:cover;vertical-align:bottom;-webkit-filter:brightness(100%);transition:all .7s ease}.gallery-list__image--poly{font-family:'object-fit: cover'}.gallery-drop{position:relative;display:-webkit-flex;display:flex;border:2px #000000 dashed;width:100%;min-height:40px;text-align:left;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:20px;cursor:pointer}.gallery-drop .gallery-drop__info{color:#8d8888;padding:10px;pointer-events:none}.gallery-drop .gallery-drop__input{position:absolute;width:100%;height:100%;opacity:0;cursor:pointer}@media (max-width: 880px){.gallery{padding:0 10px}}@media (max-height: 680px){.gallery-list__item{height:40vh}}@media (max-height: 480px){.gallery-list__item{height:60vh}}@media (max-aspect-ratio: 1 / 1) and (max-width: 480px){.gallery-list{-webkit-flex-direction:row;flex-direction:row}.gallery-list__item{height:auto;width:100%}.gallery-list__image{width:100%;min-width:0}}@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none){.gallery-header__input{width:calc(100% - 130px)}.gallery-header__button{width:70px}.gallery-list__item{height:25vh;margin-right:5px;margin-bottom:5px}}

